let overflowcount = 3;//item
let deltaPos = 0;// content
let checkTimes = 0;//  item item 
/**scrollviewitem,item*/
cc.Class({
    extends: cc.ScrollView,
    editor: CC_EDITOR && {
        menu: 'i18n:UIUtil/ScrollViewLoop'
    },
    properties: {
        itemNode: {
            default: null,
            type: cc.Node,
            tooltip: "node"
        },
        itemprefab: {//
            default: null,
            type: cc.Prefab,
            tooltip: ""
        },
        viewMask: {//mask
            type: cc.Node,
            default: null,
            tooltip: "node"
        },
        spacing: {//
            default: 3,
            tooltip: ""
        },
        /*--------------- -----------------*/
        mDirect: {
            default: "x",
            visible: false
        },//"x" / "y"
        mValue: {
            default: "width",
            visible: false
        },//"width" / "height"
        mAnchor: {
            default: "anchorX",
            visible: false
        },//"anchorX" / "anchorY"
        dataCount: {
            default: 0,
            visible: false
        },
        maxItemCount: {//item
            default: overflowcount,
            visible: false
        },
        curItemCount: {//item
            default: 0,
            visible: false
        },
        curShowItemIndex: {//data
            default: [],
            visible: false
        },
        showTopPos: {//
            default: 0,
            visible: false
        },
        showDownPos: {//
            default: 0,
            visible: false
        },
        hideTopPos: {//
            default: 0,
            visible: false
        },
        hideDownPos: {//
            default: 0,
            visible: false
        },
        itemActiveList: {//item
            default: [],
            visible: false
        },
        itemDeactivePool: {//item
            default: null,
            type: cc.NodePool,
            visible: false
        },
        lastContentPos: {//content x/y
            default: 0,
            visible: false
        },
        checkdelta: {//
            default: 0,
            visible: false
        },
        itemway: {
            default: -1,
            visible: false
        },
        itemSynPos: {
            default: new cc.Vec2(0, 0),
            visible: false
        },
        cloneItem: {
            default: null,
            visible: false
        }
    },

    onLoad() {
        this.cloneItem = this.itemNode || this.itemprefab;
        this.itemDeactivePool = [];
        for (let index = 0; index < overflowcount; index++) {
            let item = cc.instantiate(this.cloneItem);
            item.active = false;
            this.content.addChild(item)
            this.itemDeactivePool.push(item);
        }
        this.curShowItemIndex = [0, 0];
        //,
        this.vertical = !this.horizontal;
        this.mDirect = this.horizontal ? "x" : "y";
        this.mValue = this.horizontal ? "width" : "height";
        this.mAnchor = this.horizontal ? "anchorX" : "anchorY";
        this.itemway = this.horizontal ? 1 : -1;
        this.lastContentPos = this.content[this.mDirect];
        this.CalculateRangePos();
    },

    update(dt) {
        this._super(dt);
        if (this.isScrolling() || this.isAutoScrolling())
            this.UpdateItemPos();
    },
    /***/
    CalculateRangePos() {
        this.checkdelta = 0.25 * this.spacing;
        //item
        this.maxItemCount = Math.floor(this.viewMask[this.mValue] / this.spacing) + overflowcount;
        //
        this.showTopPos = this.lastContentPos - this.itemway * this.spacing;
        this.showDownPos = this.lastContentPos + this.itemway * (this.viewMask[this.mValue] + 0.5 * this.spacing);
        this.hideTopPos = this.lastContentPos - this.itemway * 1.5 * this.spacing;
        this.hideDownPos = this.lastContentPos + this.itemway * (this.viewMask[this.mValue] + 1.5 * this.spacing);
    },
    /**item */
    GetItem() {
        var item = this.itemDeactivePool.pop();
        if (!item) {
            item = cc.instantiate(this.cloneItem);
            this.content.addChild(item);
        }
        item.active = true;
        return item;
    },
    /**item*/
    PopItem() {
        if (this.curItemCount >= this.maxItemCount)
            return;
        var item = this.GetItem();
        this.itemActiveList.push(item);
        this.itemSynPos[this.mDirect] = this.itemway * this.curShowItemIndex[1] * this.spacing;
        item.setPosition(this.itemSynPos);
        if (this.freshItemCallback)
            this.freshItemCallback(this.curShowItemIndex[1], item);
        this.curShowItemIndex[1]++;
        this.curItemCount++;
    },
    /**item*/
    PushItem() {
        var item = this.itemActiveList.pop();
        this.itemDeactivePool.push(item);
        this.curShowItemIndex[1]--;
        this.curItemCount--;
    },
    /**item*/
    UnshiftItem() {
        var item = this.GetItem();
        this.itemActiveList.unshift(item);
        this.curShowItemIndex[0]--;
        this.itemSynPos[this.mDirect] = this.itemway * this.curShowItemIndex[0] * this.spacing;
        item.setPosition(this.itemSynPos);
        if (this.freshItemCallback)
            this.freshItemCallback(this.curShowItemIndex[0], item);
        this.curItemCount++;
    },
    /**item*/
    ShiftItem() {
        var item = this.itemActiveList.shift();
        item.active = false;
        this.itemDeactivePool.push(item);
        this.curShowItemIndex[0]++;
        this.curItemCount--;
    },
    /***/
    CheckTopPop() {
        if (this.horizontal) {
            if (this.content[this.mDirect] + this.itemActiveList[0][this.mDirect] < this.hideTopPos)
                this.ShiftItem();
        }
        else if (this.content[this.mDirect] + this.itemActiveList[0][this.mDirect] > this.hideTopPos) {
            this.ShiftItem();
        }
    },
    /***/
    CheckTopPush() {
        if (this.curShowItemIndex[0] == 0)
            return;
        if (this.horizontal) {
            if (this.content[this.mDirect] + this.itemActiveList[0][this.mDirect] > this.showTopPos)
                this.UnshiftItem();
        }
        else if (this.content[this.mDirect] + this.itemActiveList[0][this.mDirect] < this.showTopPos) {
            this.UnshiftItem();
        }
    },
    /***/
    CheckDownPop() {
        if (this.horizontal) {
            if (this.content[this.mDirect] + this.itemActiveList[this.curItemCount - 1][this.mDirect] > this.hideDownPos)
                this.PushItem();
        }
        else if (this.content[this.mDirect] + this.itemActiveList[this.curItemCount - 1][this.mDirect] < this.hideDownPos) {
            this.PushItem();
        }
    },
    /***/
    CheckDownPush() {
        if (this.curShowItemIndex[1] == this.dataCount) {
            return;
        }
        if (this.horizontal) {
            if (this.content[this.mDirect] + this.itemActiveList[this.curItemCount - 1][this.mDirect] < this.showDownPos) {
                this.PopItem();
            }
        }
        else if (this.content[this.mDirect] + this.itemActiveList[this.curItemCount - 1][this.mDirect] > this.showDownPos) {
            this.PopItem();
        }
    },
    /**item*/
    UpdateItemPos() {
        deltaPos = this.content[this.mDirect] - this.lastContentPos;
        if (Math.abs(deltaPos) < this.checkdelta)
            return;
        checkTimes = Math.ceil(Math.abs(deltaPos) / this.spacing)
        while (checkTimes > 0) {
            checkTimes--;
            if (this.horizontal) {
                if (deltaPos < 0 && this.curShowItemIndex[1] < this.dataCount) {//
                    this.CheckTopPop();//
                    this.CheckDownPush();//
                }
                else if (deltaPos > 0 && this.curShowItemIndex[0] > 0) {//
                    this.CheckDownPop();//
                    this.CheckTopPush();//
                }
            }
            else {
                if (deltaPos > 0 && this.curShowItemIndex[1] < this.dataCount) {//
                    this.CheckTopPop();//
                    this.CheckDownPush();//
                }
                else if (deltaPos < 0 && this.curShowItemIndex[0] > 0) {//
                    this.CheckDownPop();//
                    this.CheckTopPush();//
                }
            }
        }
        this.lastContentPos = this.content[this.mDirect];
    },

    /*------------------------------------------------------------------*/
    /**item  */
    InitItemData(count, callback, self) {
        if (self)
            this.freshItemCallback = callback.bind(self);
        else
            this.freshItemCallback = callback;
        this.dataCount = count;
        this.content[this.mValue] = this.dataCount * this.spacing;
        if (count > this.maxItemCount)
            count = this.maxItemCount;
        for (let index = 0; index < count; index++) {
            this.PopItem();
        }
    },
    /**item*/
    AddItemData() {
        if (this.curShowItemIndex[1] == this.dataCount) {
            this.PopItem();
        }
        this.dataCount++;
        this.content[this.mValue] = this.dataCount * this.spacing;
    },
    /**item */
    AddItemDataNum(num) {
        for (let i = 0; i < num; i++) {
            this.AddItemData();
        }
    },
    /**item    index: */
    DeleteItem(index) {
        this.stopAutoScroll();
        if (index < this.curShowItemIndex[0]) {//
            this.content[this.mDirect] -= this.spacing;
        }
        index = index - this.curShowItemIndex[0];
        for (let i = index < 0 ? 0 : index; i < this.itemActiveList.length; i++) {
            const element = this.itemActiveList[i];
            this.freshItemCallback(this.curShowItemIndex[0] + i, element)
        }
        this.dataCount--;
        this.content[this.mValue] = this.dataCount * this.spacing;
        if (this.content[this.mValue] < this.content[this.mDirect] + this.viewMask[this.mValue]) {
            this.scrollToBottom();
        }
    },
    /**item*/
    UpdateItem(index) {
        if (this.curShowItemIndex[0] <= index <= this.curShowItemIndex[1]) {
            if (this.freshItemCallback) {
                let _index = index - this.curShowItemIndex[0];
                this.freshItemCallback(index, this.itemActiveList[_index]);
            }
        }
    },
    /**item*/
    UnRegestUpdateItem() {
        this.freshItemCallback = null;
    },
    /***/
    ClearData(callback) {
        this.stopAutoScroll();
        if (this.horizontal)
            this.itemSynPos[this.mDirect] = -this.viewMask[this.mValue] * this.viewMask[this.mAnchor]
        else
            this.itemSynPos[this.mDirect] = this.viewMask[this.mValue] * (1 - this.viewMask[this.mAnchor])

        this.content.setPosition(this.itemSynPos);
        for (let index = 0; index < this.itemActiveList.length; index++) {
            const element = this.itemActiveList[index];
            element.active = false;
            this.itemDeactivePool.push(element);
        }
        this.itemActiveList.splice(0, this.curItemCount);
        this.curItemCount = 0;
        this.content.height = 0;
        this.curShowItemIndex = [0, 0];
        this.lastContentPos = this.itemSynPos[this.mDirect];
        if (callback) callback();
    },
    /**
     *  
     * @param {*} space 
     */
    SpacingChanged(space) {
        if (this.itemActiveList.length <= 0) return;
        this.ClearData(() => {
            this.CalculateRangePos();
            this.InitItemData(this.dataCount, this.freshItemCallback);
        })
    },
    
    //item
    setItemNode(item){
        this.content.removeAllChildren();
        this.itemNode = item;
        this.cloneItem = this.itemNode || this.itemprefab;
        this.itemDeactivePool = [];
        for (let index = 0; index < overflowcount; index++) {
            let item = cc.instantiate(this.cloneItem);
            item.active = false;
            this.content.addChild(item)
            this.itemDeactivePool.push(item);
        }
        this.curShowItemIndex = [0, 0];
        //,
        this.vertical = !this.horizontal;
        this.mDirect = this.horizontal ? "x" : "y";
        this.mValue = this.horizontal ? "width" : "height";
        this.mAnchor = this.horizontal ? "anchorX" : "anchorY";
        this.itemway = this.horizontal ? 1 : -1;
        this.lastContentPos = this.content[this.mDirect];
        this.CalculateRangePos();
    },
})