/**
 *  
 *   
 */
import lottery_LanguageManager from "lottery_LanguageManager";

cc.Class({
    extends: cc.Component,

    properties: {
        languageID: {
            default: "",
            tooltip: "key"
        },
        languageManager: {
            default: null,
            visible: false
        },
        curLanguageEnum: {
            default: 0,
            visible: false
        },
        changeLanguageEnum: {
            default: 1,
            visible: false
        },
        args: {
            default: [],
            visible: false
        },
    },
    onLoad() {
        this.languageManager = lottery_LanguageManager.getInstance();
        this.languageManager.Register(this.node.uuid, this.changeLanguage, this);
    },
    onEnable() {
        if (this.curLanguageEnum !== this.changeLanguageEnum) {
            this.changeLanguage(this.changeLanguageEnum)
        }
    },
    onDestroy() {
        this.languageManager.UnRegister(this.node.uuid);
    },
    changeLanguage(lang) {
        if (!this.node.active) {
            this.changeLanguageEnum = lang
            return;
        }
        this.curLanguageEnum = this.changeLanguageEnum;
        if (this.languageID)
            this.UpdateText();
    },
    /** */
    UpdateText() {

    },
    /**ID */
    SetLanguageID(id) {
        this.languageID = id;
        if (this.languageID)
            this.UpdateText();
    },
    /**  arguments*/
    SetArgs() {
        this.args = arguments;
        if (this.languageID)
            this.UpdateText();
    }
});