const videoStageTime = [16, 10, , 8, 7, 10]; //16+10+8+7+10 = 51
//,,
export default class videosMsgFatory {
    static _root;
    static ins() {
        if (!this._root)
            this._root = new videosMsgFatory();
        return this._root;
    }

    //
    stanardFlow = [[1], [2], [3], [4], [5], [6, 7]];
    //
    _msgList = [];
    //
    period = 2; // -1 0  1  ,2

    /** */
    firstToDay() {
        this._msgList = this.anlyArr([[1, 10000]]);
    }
    /***/
    endToDay() {
        this._msgList = [];
    }
    /***/
    commonToDay() {
        this._msgList = this.anlyArr(this.stanardFlow);
    }
    /***/
    closePanToDay() {
        this._msgList = this.anlyArr([[8, 3000]]);
    }
    /***/
    get msgList() {
        switch (this.period) {
            case -1:
                this.firstToDay();
                break;
            case 0:
                this.commonToDay();
                break;
            case 1:
                this.endToDay();
                break;
            case 2:
                this.closePanToDay();
                break;
            default:
                this._msgList = [];
                break;
        }
        return this._msgList;
    }
    /***/
    set countDownTime(time) {
        for (let one of this._msgList) {
            if (one.type == 2) {
                one.data = time;
                return;
            }
        }
        console.log("");
    }
    /** */
    anlyArr() {
        let arr = [];
        for (let one of arguments[0]) {
            arr.push(new videosMsg(one[0], one[1]));
        }
        return arr;
    }
    /***/
    changePeriod(data) {
        this.period = data;
    }
}

//
class videosMsg {
    type;//
    data;//
    constructor(type, data) {
        this.type = type;
        this.data = data;
    }
}